import React, { Component } from "react";
import { dati, AGGIORNAMENTO, DATA_AGGIONAMENTO, COMMON_PROPS } from '../assets/dati';
import Grafico from "./Grafico";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Grid from '@material-ui/core/Grid';
import moment from "moment";

let giorni = (ultimoMese) => {
    let etichette = [];
    let giornoInizio = null;
    if (ultimoMese) {
        giornoInizio = moment().subtract(3, 'weeks');
    } else {
        giornoInizio = moment("20200101", "YYYYMMDD");
    }

    let ieri = moment(DATA_AGGIONAMENTO + " 23:59:ss", "DD/MM/YYYY HH:mm:ss");
    while (giornoInizio <= ieri)  {
        etichette.push(giornoInizio.format("DD/MM/YYYY"));
        giornoInizio.add(1, 'days');
    } 

    return etichette;

}


let recuperaDatasetPaese = (paese, tipo, ultimoMese, gg) => {


    let datiPaese = dati.filter(element => element.CountryExp === paese);

    let datiPaesePerTipo = gg.map((giorno) => {
        let dato = datiPaese.find((elem) => elem.DateRep === giorno);
        if (dato == null)
            return 0;
        return dato[tipo];

    });

    let l_borderColor = "";
    let l_backGroundColor = "";

    switch (paese) {
        case "Italy":
            l_borderColor = '#8000ff';
            l_backGroundColor = "#d9b3ff";
            break;

        case "Spain":
            l_borderColor = '#e68a00';
            l_backGroundColor = "#ffe0b3";
            break;

        case "France":
            l_borderColor = '#3399ff';
            l_backGroundColor = "rgba(75,192,192,0.4)";
            break;

        case "Germany":
            l_borderColor = '#00b33c';
            l_backGroundColor = "#ccffdd";
            break;

        case "China":
            l_borderColor = '#ff0066';
            l_backGroundColor = "#ffb3d1";
            break;

        default:
            l_backGroundColor = "#d9b3ff";
            l_borderColor = 'black';

    }

    

    let x = {
        label: paese,
        data: datiPaesePerTipo,

        backgroundColor: l_backGroundColor,
        borderColor: l_borderColor,

    };

    return (
        Object.assign(x, COMMON_PROPS)
    )
}



class Main extends Component {


    constructor(props) {
        super(props);

        this.state = {
            tipo: "NewDeaths",
            ultimoMese: true,
            showItaly: true,
            showChina: false,
            showFrance: true,
            showGermany: true,
            showSpain: true,

        }

    }


    componentDidMount() {
    }



    ////////////////////////////////////////////////////////////////




    handleCheck = name => event => {
        this.setState({ [name]: event.target.checked });
    };



    handleChangeTipo = name => event => {
        this.setState({ tipo: event.target.value });
    };



    handleChangeUltimoMese = name => event => {
        this.setState({ ultimoMese: event.target.checked });
    };


    renderCheck = nome => {
        let nomeCheck = "show" + nome;
        let valoreStato = this.state[nomeCheck];

        return (

            <FormControlLabel
                control={
                    <Switch checked={valoreStato}
                        onChange={this.handleCheck(nomeCheck)} value={nomeCheck}
                        color="primary"
                    />
                }
                label={nome}
            />


        );
    };

    ////////////////////////////////////


    ricalcolaGrafico = () => {

        let newDataSets = [];

        let gg = giorni(this.state.ultimoMese);


        if (this.state.showGermany) {
            newDataSets.push(recuperaDatasetPaese("Germany", this.state.tipo, this.state.ultimoMese, gg));
        }
        if (this.state.showFrance) {
            newDataSets.push(recuperaDatasetPaese("France", this.state.tipo, this.state.ultimoMese, gg));
        }
        if (this.state.showSpain) {
            newDataSets.push(recuperaDatasetPaese("Spain", this.state.tipo, this.state.ultimoMese, gg));
        }
        if (this.state.showItaly) {
            newDataSets.push(recuperaDatasetPaese("Italy", this.state.tipo, this.state.ultimoMese, gg));
        }
        if (this.state.showChina) {
            newDataSets.push(recuperaDatasetPaese("China", this.state.tipo, this.state.ultimoMese, gg));
        }

        return ({
            labels: gg,
            datasets: newDataSets
        });


    }





    render() {



        return (
            <Grid
                container
                direction="column"
                justify="space-evenly"
                alignItems="stretch"
            >
                <h1 className="titolo">Situazione contagi / decessi per Coronavirus al {AGGIORNAMENTO}</h1>

                <fieldset className="opzioni">
                    <legend>Opzioni</legend>

                    <FormGroup row>
                        {this.renderCheck("Italy")}
                        {this.renderCheck("France")}
                        {this.renderCheck("China")}
                        {this.renderCheck("Germany")}
                        {this.renderCheck("Spain")}
                    </FormGroup>

                    <RadioGroup row value={this.state.tipo} name="tipo" onChange={this.handleChangeTipo("tipo")}>
                        <FormControlLabel value="NewDeaths" control={<Radio />} label="Nuovi decessi" />
                        <FormControlLabel value="NewConfCases" control={<Radio />} label="Nuovi contagi" />
                    </RadioGroup>
                    <FormGroup row>

                        <FormControlLabel
                            control={
                                <Switch checked={this.state.ultimoMese}
                                    onChange={this.handleChangeUltimoMese("ultimoMese")} value="ultimoMese" name="ultimoMese"
                                    color="secondary"
                                />
                            }
                            label="Limita i dati alle ultime tre settimane"
                        />
                    </FormGroup>


                </fieldset>

                <div className="grafico">
                    <Grafico data={this.ricalcolaGrafico()}></Grafico>
                </div>
                <p className="alert">Attenzione! L'applicazione  ancora in  fase di sviluppo per cui sono possibili errori e malfunzionamenti</p>

                <p><span className="avviso">I dati sono stati resi disponibili
                da <br></br><a href="https://www.ecdc.europa.eu/en/publications-data/download-todays-data-geographic-distribution-covid-19-cases-worldwide">European Centre
                for Disease Prevention and Control</a></span>
                    <br></br><br></br>

                 Chart CV - Versione 0.4<br></br>
                    <a href="mailto:franco.vuoto@gmail.com">Franco Vuoto</a> - Bologna, 14/03/2020</p>

                <p> </p>



            </Grid>
        );
    }
}

export default Main;

